.imageCarousel {
	display: block;
	width: auto;
	height: 250px;
	margin-right: 5px;
  }
.carouselContainer{
	display: flex;
	overflow-x: scroll;
	width: 100%;
	background-color: #E6E6E6;
}
.overlayBlack{
	position: absolute; 
	top: 0; 
	background-image: linear-gradient(to bottom,black, rgba(0,0,0,0));
	height: 50px; 
	max-width: 100%;
	width: 100%;
	opacity:1;
}
.overlayContainer {
	position: relative;
	max-width: 100%;
	background-color: #E6E6E6;
  }
.backIcon{
	margin-top: 20px;
	margin-left: 20px;
}
.displayTitle{
	margin: 10px 10px;
	max-width: 100%;
	font-weight: 300;
	display: flex;
}
.mapIcon{
	margin-left: auto;
	margin-right: 20px;
}
.displaySubtitle{
	margin: 0px 10px;
	max-width: 100%;
	font-weight: 300;
	font-size: small;
	display: flex;
}
.classCard{
	border-radius: 5px;
	height: 100px;
	width: auto;
	background-color: white;
	margin: 20px 10px;
	text-align: center;
	font-size: 15px;
	font-weight: 300;
	padding: 10px 10px;
}
.offers{
	height: auto;
	padding: 6px 8px;
	width: 100px;
	background-color: rgba(4, 191, 122, 0.568);
	margin: 5px 10px;
	border-radius: 5px;
	font-size: 12px;
	display: flex;
	visibility: hidden;
}
.width_{
	width: 100px;
	margin: 0px 10px;
}
.displaySubtitle2{
	margin: 10px 0px 0px 10px;
	max-width: 100%;
	font-weight: 500;
	font-size: small;
	display: flex;
}
.list{
	padding: 5px;
	margin: 5px;
	border-radius: 5px;
	width: auto;
	height: 40px;
	display: inline-block;
	background-color: #04BF7B;
	color: white;
}
.qualifications{
	margin: 0px 10px;
	max-width: 100%;
	font-weight: 300;
	font-size: small;
	color: #043540;
}